.Search-input {
  font-size: 16px;
  padding: 16px 32px;
  color: #566370;
  background-color: #f0f3f4;
  width: 25vw;
  border: none;
  border-radius: 24px;
  margin-bottom: 10px;
}
.Search-input:focus {
  border: 1px solid #1d9bf0;
  outline: none;
  background-color: white;
  color: #101419;
}
